<template>
    <Layout class="home-box">
        <Card :style="{width: '90%', margin: '0 auto'}">
            <p slot="title">Select location</p>
            <a href="#" slot="extra" @click.prevent="confirm">
                <Icon type="md-checkbox-outline" />
                Confirm
            </a>
            <a href="#" slot="extra" @click.prevent="$router.push({name: 'ShopEdit'})" :style="{marginLeft: '10px'}">
                <Icon type="md-arrow-back" />
                Back
            </a>
            <GmapMap
                :center="mapCenter"
                :zoom="mapZoom"
                map-type-id="terrain"
                style="width: 100%; height: calc(100vh - 370px);"
                @center_changed="centerUpdated($event)"
            >
                <GmapMarker
                    :position="markerPosition" />
            </GmapMap>
        </Card>
    </Layout>
</template>
<script>
import _ from 'lodash'

export default {
    name: 'WorldMap',
    data: function () {
        return {
            mapZoom: 7,
            mapCenter: {
                lat: 24.723628,
                lng: 46.672808
            },
            markerPosition: {
                lat: 24.723628,
                lng: 46.672808
            }
        }
    },
    created: function () {
        this.getCenter()
    },
    methods: {
        getCenter: function () {
            if (this.$config.appInfo) {
                if (this.$config.appInfo.shop.coordinates.length === 2 && this.$config.appInfo.shop.coordinates[0] !== 0) {
                    this.mapCenter = {
                        lat: this.$config.appInfo.shop.coordinates[0],
                        lng: this.$config.appInfo.shop.coordinates[1]
                    }
                    this.mapZoom = 18
                    this.markerPosition = _.clone(this.mapCenter)
                }
            }
        },
        centerUpdated: function (event) {
            this.markerPosition.lat = event.lat()
            this.markerPosition.lng = event.lng()
        },
        confirm: function () {
            this.$config.shopCache.shopInfo.X = this.markerPosition.lat.toFixed(6)
            this.$config.shopCache.shopInfo.Y = this.markerPosition.lng.toFixed(6)
            this.$router.push({name: 'ShopEdit'})
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
        height: 100%;
    }
    .ivu-card-body {
        height: 100% !important;
    }
</style>



